<template>
  <div>
    <h3>{{datasetDescription}}</h3>
    <table>
      <th>
        <td>城市</td>
        <td>氣溫</td>
      </th>
      <tr v-for="item in location" :key="item.id">
        <td>{{item.locationName}}</td>
        <td>
          {{item.weatherElement[2].time[0].parameter.parameterName}} -
          {{item.weatherElement[4].time[0].parameter.parameterName}} °C
        </td>
      </tr>
    </table>
    <p>1. <a href="https://opendata.cwa.gov.tw/index">氣象資料開放平台</a>，加入會員取得API授權碼<br>
    2. <a href="https://opendata.cwa.gov.tw/dist/opendata-swagger.html">中央氣象署開放資料平臺</a>之資料擷取API</p>
  </div>

</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Weather',
  created () {
    this.$store.dispatch('loadData')
  },
  computed: {
    ...mapGetters({ weatherApi: 'getWeather' }),
    weatherData () {
      return JSON.parse(JSON.stringify(this.weatherApi))
    },
    weatherDataLen () {
      return Object.keys(this.weatherData).length
    },
    datasetDescription () {
      return (this.weatherDataLen > 0) ? this.weatherApi.data.records.datasetDescription : ''
    },
    location () {
      return (this.weatherDataLen > 0) ? this.weatherApi.data.records.location : ''
    }
  }
}
</script>

